@import (less) "color.less";

.iradio-container {
    margin-right: 0.625em;
}
.iradio-container.checked {
  .radio-label {
    color: @green !important;
  }
  .radio-label-icon {
    color: @green !important;
  }
  .radio-icon {
    color: @green !important;
  }
}
.iradio-container.disabled {
  .radio-label {
    color: @light_gray !important;
  }
}
.iradio_intesa {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    padding: 0;
    width: 16px;
    height: 17px;
    background: url(/cjlib/assets/images/intesa.png) no-repeat;
    border: none;
    background-position: -72px 0;
    input[type="radio"] {
        margin: 4px 4px 0 0 !important;
        cursor: pointer;
        opacity: 0;
        &:disabled {
            cursor: auto;
        }
    }
    &.yellowborder {
        background-position: -269px 0;
    }
}
.iradio_intesa_spunta.checked {
    background-position: -162px 0 !important;
    &.yellowborder {
        background-position: -305px 0 !important;
    }
}
.iradio_intesa_spunta.checked.disabled {
    background-position: -144px 0 !important;
    &.yellowborder {
        background-position: -340px 0 !important;
    }
}
.iradio_intesa.checked {
    background-position: -90px 0;
    &.yellowborder {
        background-position: -287px 0 ;
    }
}
.iradio_intesa.disabled {
    background-position: -72px 0;
    cursor: default;
    &.yellowborder {
        background-position: -251px 0 ;
    }
}
.iradio_intesa.checked.disabled {
    background-position: -126px 0;
    &.yellowborder {
        background-position: -322px 0;
    }
}
.radio-label {
    color: @gray;
    font-size: 0.750em;
    margin: 0 0 0 5px !important;
    // margin: 0 !important;
}

.radio-label-icon {
    padding-left: 0.825em;
    padding-bottom: 0.8125em;
    display: table-cell !important;
    vertical-align: middle;
}

.radio-icon {
    font-size: 2em !important;
    vertical-align: middle;
    margin: 5px 0 0 15px !important;
}

.radio-error-message {
    color: @red;
    font-size: 0.750em;
    margin-top: 0;
    margin-bottom: 0.625em;
    opacity: 1;
}
.radio-error-message.ng-hide-add {
    transition: all ease-out 0.25s;
}
.radio-error-message.ng-hide-remove {
    transition: all ease-out 0.25s;
}
.radio-error-message.ng-hide {
    opacity: 0;
    margin-top: -0.625em;
}
@media (-o-min-device-pixel-ratio:5/4) {
    .iradio_intesa {
        background-image: url(/cjlib/assets/images/intesa@2x.png);
        -webkit-background-size: 645px 19px;
        background-size: 645px 19px;
  
    }
}
@media (-webkit-min-device-pixel-ratio:1.25) {
    .iradio_intesa {
        background-image: url(/cjlib/assets/images/intesa@2x.png);
        -webkit-background-size: 645px 19px;
        background-size: 645px 19px;
    }
}
@media (min-resolution:120dpi) {
    .iradio_intesa {
        background-image: url(/cjlib/assets/images/intesa@2x.png);
        -webkit-background-size: 645px 19px;
        background-size: 645px 19px;
    }
}
@media (min-resolution:1.25dppx) {
    .iradio_intesa {
       background-image: url(/cjlib/assets/images/intesa@2x.png);
        -webkit-background-size: 645px 19px;
        background-size: 645px 19px;
    }
}

